export function numberGenerator(arr, number) {
  if (arr.length >= number) return;
  let newNumber = Math.floor(Math.random() * number);
  if (arr.indexOf(newNumber) < 0) {
    arr.push(newNumber);
  }
  numberGenerator(arr, number);
}

export default function showBlocks() {
  try {
    const elementsShow = {
      choiceWrapper: document.querySelector(".choice__wrapper"),
      cat: document.querySelector("section.causes > .container"),
      causesItem: document.querySelectorAll(".causes__item"),
      diagramBox: document.querySelector(".diagram__box"),
      element: document.querySelector(".puch"),
    };

    const puch = document.querySelectorAll(".puch");

    let anArrayOfUniqueNumbers = [];
    numberGenerator(anArrayOfUniqueNumbers, elementsShow.causesItem.length);

    let oldScrollTopPosition = 0;
    let startPos = 0;

    window.addEventListener("scroll", () => {
      try {
        const setions = {
          choice: document.querySelector(".choice").getBoundingClientRect(),
          causes: document.querySelector(".causes").getBoundingClientRect(),
          diagramBox: document.querySelector(".diagram__box").getBoundingClientRect(),
        };

        if (setions.choice.top <= 700) {
          elementsShow.choiceWrapper.classList.add("show");
        }

        if (setions.causes.top <= 200) {
          elementsShow.cat.classList.add("show");

          anArrayOfUniqueNumbers.forEach((item, i) => {
            elementsShow.causesItem[item].classList.add("show");
            elementsShow.causesItem[item].style.transitionDelay = `${200 * i}ms`;
          });

          // console.log(document.querySelector(".causes").offsetHeight);
          // console.log(setions.causes.top);

          const scrollTopPosition = document.documentElement.scrollTop;
          if (document.querySelector(".causes").offsetHeight > -setions.causes.top) {
            if (oldScrollTopPosition > scrollTopPosition) {
              // scroll top
              puch[0].style.transform = `translate(500px, ${(startPos += 0.5)}px) rotate(-10deg)`;
              puch[1].style.transform = `translate(670px, ${(startPos += 0.5)}px) rotate(20deg)`;
            } else {
              // scroll down
              puch[0].style.transform = `translate(500px, ${(startPos -= 0.5)}px) rotate(-10deg)`;
              puch[1].style.transform = `translate(670px, ${(startPos -= 0.5)}px) rotate(20deg)`;
            }
          }
          oldScrollTopPosition = scrollTopPosition;
        }
        if (setions.diagramBox.top <= 200) {
          elementsShow.diagramBox.classList.add("show");
        }
      } catch (e) {
        console.log(e);
      }
    });
  } catch (e) {
    console.log(e);
  }
}
