export default function showHeaderDrop() {
  try {
    const itemDrop = document.querySelectorAll(".header__item");
    const blockDrop = document.querySelectorAll(".header__drop");

    toggleClass();

    function toggleClass() {
      try {
        itemDrop.forEach((item, i) => {
          item.addEventListener("click", () => {
            blockDrop[i].classList.toggle("show");
          });
        });
      } catch (e) {
        console.log(e);
      }
    }
  } catch (e) {
    console.log(e);
  }
}
