export default function showHiddenBlock() {
  try {
    const openBtn = document.querySelector(".hiking__title-btn");
    const showBlock = document.querySelector(".hiking__tooltip");
    let timer;

    openBtn.addEventListener("click", () => {
      show();
      clearTimeout(timer);
      timer = setTimeout(hidden, 3000);
    });

    function show() {
      try {
        showBlock.classList.remove("hidden");
        showBlock.classList.add("show");
      } catch (e) {
        console.log(e);
      }
    }

    function hidden() {
      try {
        showBlock.classList.remove("show");
        showBlock.classList.add("hidden");
      } catch (e) {
        console.log(e);
      }
    }
  } catch (e) {
    console.log(e);
  }
}
