import Modal from "./Modal";

export default class ModalWrapper extends Modal {
  constructor(btnOpen, btnClose, modal, overlayModal) {
    super(btnOpen, btnClose, modal);
    this.overlayModal = document.querySelector(overlayModal);
    this.clickOverlay();
  }

  clickOverlay() {
    this.overlayModal.addEventListener("click", () => {
      this.hidden();
    });
  }
}
