export default function animationText() {
  try {
    const nodeText = document.querySelector(".banner__desr");

    const show = () => {
      nodeText.classList.add("show");
    };

    const showPsevdo = () => {
      nodeText.classList.add("bef", "aft");
    };

    setTimeout(show, 1000)
    setTimeout(showPsevdo, 1500)
  } catch (e) {
    console.log(e);
  }
}
