import animationText from "./modules/animationText";
import showBlocks from "./modules/showBlocks";
import Modal from "./modules/Modal";
import showHeaderDrop from "./modules/showHeaderDrop";
import animationCircles from "./modules/animationCircles";
import showHiddenBlock from "./modules/showHiddenBlock";
import ModalWrapper from "./modules/ModalWrapper";

document.addEventListener("DOMContentLoaded", () => {
  "use strict";
  if (document.querySelector("main#home")) {
    animationText();
    showBlocks();
    new Modal("[data-open-menu]", "[data-close-menu]", "[data-header-modal]");
    showHeaderDrop();
    animationCircles(".circles-grop_choice");
    animationCircles(".circles-grop_banner");
    animationCircles(".circles-grop_footer");
    showHiddenBlock();
    new ModalWrapper(".articles__video-btn", ".modal-youtube__btnClose", ".modal-youtube", ".modal-youtube");
  }
});
